body{
  font-family: "Avenir Next", sans-serif;
}

a:hover {
  text-decoration: none;
}


/* Header */
.MPAGLogo {
  margin-top: 10px;
  margin-left: 50px;
  height: 100px;
}

.FixedHeader {
  background-color: #425d89;
  font-family: "Andale Mono", AndaleMono, monospace;
  height: 250px;
  padding: 20px;
  color: white;
}

.HeaderTitle {
  font-size: 30px;
  margin-left: 34%;
  margin-top: -50px;
}

.SearchBar {
  border-radius: 5px;
  border: 0px;
  text-align: center;
  width: 44%;
  height: 30px;
  margin-left: 28%;
  margin-top: 30px;
  color: black;
  font-family: "Avenir Next", sans-serif;
}

.navbar {
  margin-top: 26px;
}

.navbarOption {
  text-align: center;
  padding-top: 8px;
  width: 25%;
  height: 40px;
  background-color: #acb4c1;
  float: left;
  border: 0.5px solid black;
  font-family: "Avenir Next", sans-serif;
  color: white;
}

.navbarOption:hover {
  color: black;
}

.navbarIcon {
  height: 15px;
  width: 15px;
  margin-right: 10px;
  margin-bottom: 5px;
}

.cartQuantity:hover{
  color: red;
}


/* HomePage */
.contentBackground {
  height: 500px;
}

.puppyIMG {
  width: 500px;
  margin-left: 100px;
  margin-top: 60px;
}

.HomePageHeader {
  font-size: 22px;
  position: absolute;
  margin-left: 50%;
  margin-top: -20%;
  font-family: "Avenir Next", sans-serif;
}

.browseProductsButton {
  border-radius: 15px;
  text-align: center;
  padding-top: 10px;
  width: 200px;
  height: 40px;
  /*border: 0.5px solid black;*/
  background-color: #83a884;
  font-family: "Avenir Next", sans-serif;
  color: white;
  margin-left: 64%;
  margin-top: -15%;
}

.browseProductsButton:hover{
  color: black;
}


/* Footer */
.footerBackground {
  background-color: gray;
  width: 100%;
  height: 80px;
  margin-top: 300px;
  /*text-align: center;*/
}

.socialMediaLinks {
  float: right;
  margin-right: 100px;
}

.socialMediaIcons {
  height: 30px;
  width: 30px;
  margin-top: 25px;
  margin-left: 20px;
}

.footerText{
  color: white;
  font-family: "Avenir Next", sans-serif;
  padding-top: 30px;
  margin-left: 100px;
}


/* Products */
.product{
  outline: 0.5px solid black;
  margin-top: 1px;
}

.productImage{
  width: 160px;
  height: 160px;
  margin-left: 8%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.namePrice{
  float: right;
  margin-top: 50px;
  margin-right: 58%;
}

.name{
  color: blue;
  font-size: 16px;
}

.price{
  color: red;
  font-size: 20px;
}

.rating{
  width: 80px;
}

.viewItemButton{
  width: 150px;
  position: absolute;
  margin-left: 560px;
  margin-top: 100px;
}


/*Product Details*/
.productDetails{
  margin-top: 4%;
  margin-bottom: 5%;
}

.nameProductDetails{
  font-size: 22px;
}

.instock{
  margin-top: 20px;
  color: #219323;
  font-size: 15px;
}

.colors{
  margin-left: 33%;
}

.color{
  width: 35px;
  margin-left: 3px;
  margin-top: 3px;
}

.size{
  width: 120px;
  margin-bottom: 3px;
}

.sizes{
  margin-left: 50%;
  margin-top: -5.5%;
}

.actionButtons{
  margin-left: 68%;
  margin-top: -80px;
}

.AddToCartButton{
  width: 150px;
  margin-top: 5px;
}

.AddToWishlistButton{
  width: 150px;
}

.productDescription{
  width: 60%;
  margin-top: 100px;
  margin-left: 32%;
}


/*Shopping Cart*/
.shoppingCartItem{
  outline: 1px solid black;
  margin-top: 1px;
  width: 50%;
}

.itemInShoppingCart{
  width: 160px;
  height: 160px;
  margin-left: 8%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.removeFromCartButton{
  width: 150px;
  position: absolute;
  margin-left: 8%;
  margin-top: -80px;
}

.namePriceInCart{
  float: right;
  margin-top: 50px;
  margin-right: 45%;
}

.quantity{
  position: absolute;
  margin-left: 16%;
  margin-top: -55px;
}

.cartItemSize{
  position: absolute;
  margin-left: 240px;
  margin-top: -10px;
}

.cartItemColor{
  position: absolute;
  margin-left: 240px;
  margin-top: 10px;
}

.viewDetails{
  position: absolute;
  color: blue;
  margin-left: 15.5%;
  margin-top: -20px;
}

.shoppingCartCheckout{
  position: absolute;
  margin-left: 950px;
  margin-top: 100px;
  text-align: center;
}

.checkoutHeader{
  font-size: 20px;
}

.checkoutSubtotal{
  font-size: 15px;
}

.checkoutButton{
  width: 240px;
  margin-top: 50px;
}

.selected{
  outline: 2px solid black;
}

/* Cart Navbar Dropdown */
/*START OF W3*/
.dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    /*border: none;*/
    /*cursor: pointer;*/
}

.dropdown {
    position: relative;
    /*display: inline-block;*/
}

.dropdown-content {
    display: none;
    position: absolute;
    font-family: "Avenir Next", sans-serif;
    background-color: #f9f9f9;
    /*min-width: 160px;*/
    /*box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);*/
    z-index: 1;
    width: 345px;
    margin-left: 75%;
    margin-top: 3%;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {
  /*background-color: #f1f1f1; */
}

.dropdown:hover .dropdown-content {
    display: block;
    /*float: right;*/
}

.dropdown:hover .dropbtn {
    background-color: #3e8e41;
}
/*END OF W3*/

.previewImg{
  width: 25%;
  float: right;
  /*margin-left: 35%;*/
  margin-top: -25%;
  /*padding-bottom: 10px;*/
}

.previewDescription{
  font-size: 12px;
  border: 2px solid outline;
}

